.canvasjs-chart-credit
{
    display: none !important;
}

.custom-credit {
    position: absolute;
    bottom: 7px;
    right: 10px;
    font-size: 12px;
    color: #555;

    a {
        text-decoration: none;
        color: inherit; // Match the text color

        &:hover {
            text-decoration: underline; // Optional: underline on hover
        }
    }
}