.App{
    padding: 10px;
    background-image: linear-gradient(to right,#ffffff,#04b7c0);
}
.App[contenteditable="true"]:focus{
    outline: 2px solid #00f;
}
.title{
    text-align: center;
    

    h1{
        font-size: xxx-large;
    }
    p{
        font-size: 20px;
        font-style: italic;
        direction: rtl;
        font-weight: bold;
    }
}

.body{
    
    text-align: center;

    padding-left: 10%;
    padding-right: 10%;
    span{
        direction: rtl;
        width: 100%;
        display :block;
    }
}


.liked{

    
    h2{
        text-align: center;
        font-size: 25px;
    }
    li{
        direction: rtl;
        
        display :block;
        text-align: right;
        list-style-type: disc;
    }
}
customer_liked{
    li{
        list-style-type: disc;
    }
}
.chart-container{
    display: flex;
    width: 150%;
    justify-content: center;
    
}

.chart{
    display: inline-block;
    width: 600px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
    flex: 1; /* Equal width for both divs */
    
    
    text-align: center;
    margin: 5px; /* Optional: Adds some spacing between the boxes */
}